* {
    text-align: center;
}

h1 {
    font-style: initial;
    font-size: 3rem;
    margin-bottom: 0rem;
}

li {
    display: inline-block;
    padding-left: 4px;
    padding-right: 4px;
}

a {
    text-decoration: none;
    color: #000;
    padding: 2px;
}

/* Header */
#shelf {
    color: #ffcc66;
}


/* Stand Alone Pages */
/* Home Page */
.btnSpace {
    margin: 0 2px;
}
.hero {
    text-align: center;
    display: block;
    margin: auto;
    margin-top: 20px;
    /* Use "linear-gradient" to add a darken background effect to the image (photographer.jpg). This will make the text easier to read */
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("https://cdn.pixabay.com/photo/2017/08/01/10/44/people-2564463_1280.jpg");
    /* Set a specific height */
    height: 600px;
    width: 1200px;
    /* border: black solid 2px;
    border-radius: 5%; */
    /* Position and center the image to scale nicely on all screens */
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: auto;
    position: relative;
  }
  
  /* Place text in the middle of the image */
  .hero-text {
    /* border-radius: 10%;
    border: 2px solid #ffcc66; */
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
  }

  /* Search Component */
.search-container {
    padding: 2% 9%;
}

.search-form {
    justify-content: center;
    padding: 5px;
}

.search-field {
    margin: 1px;
}

/* Pagination */
.pagination {
    list-style-type: none;
    font-size: 1.2rem;
    margin: auto;
}

.active {
    font-size: 1.1rem;
    color: #ffcc66;
    text-decoration: underline #ffcc66;
}

/* Books */
.bookDescription {
    max-height: 200px;
    overflow: auto;
}

.bookPrice {
    border: thin solid #ffcc66;
    border-radius: 100%;
}
